.container {
  max-width: 1170px;
  margin: 0 auto;
}

.dark {
  color: #e6e0e0;
  background-color: #606060;
}

.translate__swich {
  transform: translateX(15px) !important;
}

.list_api {
  flex-wrap: wrap;
  justify-content: center;
  gap: 40px;
  margin-top: 6%;
  display: flex;
}

.card {
  cursor: pointer;
  border: .5px solid #c2c2c2;
  border-radius: 10px;
  flex-basis: calc(33.3333% - 26.6667px);
  max-width: 350px;
  padding: 20px 25px;
  line-height: 1em;
}

.card:hover {
  box-shadow: 0 6px 24px 9px #00000080;
}

.card > img {
  width: 300px;
}

.generes {
  color: #8f1021;
  margin-bottom: 10px;
  font-weight: 600;
}

.dark .generes {
  color: #da8f1f;
}

@media (min-width: 768px) and (max-width: 1200px) {
  .list_api {
    gap: 38px;
  }

  .card {
    flex-basis: calc(50% - 19px);
  }
}

#titleMylist {
  text-align: center;
  margin-top: 50px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 30px;
}

.mylistBTN {
  color: #f0f8ff;
  background-color: #750b0b;
  border: .5px solid #570303;
  border-radius: 20px;
  width: 50%;
  height: 40px;
  margin-top: 10px;
  font-weight: 700;
}

.mylistBTN:hover {
  cursor: pointer;
  color: #f0f8ff;
  background-color: #ac2727;
  border: .5px solid #ff3737;
  border-radius: 20px;
  font-weight: 700;
  box-shadow: 0 6px 24px 9px #00000080;
}

.addedBTN {
  color: #000;
